export default {
	data() {
		return {
			br:'\n',
			title:'',
			apt_site:'',
			apt_date:'',
			
			rtData:[]
			
			
			
		}
	},
	methods: {
		
	},
	onShow:function(){
		let userinfo = this.getUserInfo();
		let uid = userinfo.id;
		uni.request({
			url: this.createApiUrl('appointment/get/uid'),
			method: 'GET',
			data: {uid:uid},
			success: res => {
				console.log(res);
				res = res.data.data;
				this.rtData = res;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
